import { showAddress } from 'api/api'
import { mapGetters, mapActions } from 'vuex'

export default {
  data() {
    return {
      address:[]
    }
  },
  created() {
    this._getAddress()
  },
  computed: {
    ...mapGetters([
      'userAccount'
    ])
  },
  methods: {
    back() {
      this.$router.back()
    },
    newAddress() {
      this.$router.push({
        path: '/newaddress'
      })
    },
    choose(item,index) {
      // console.log(index)
      // console.log(this.address[index])
      // 删除，并且重新添加到数组第一项
      let val = this.address[index]
      this.address.splice(index, 1)
      this.address.unshift(val)
      console.log(this.address)
      this.saveAddress(this.address)
      this.$router.back()
    },
    _getAddress() {
      let token = this.userAccount.token
      let uid = this.userAccount.uid
      showAddress(token,uid).then((res)=>{
        if(res.status === 1) {
          this.address = res.msg
        }
      })
    },
     ...mapActions([
      'saveAddress'
    ])
  }
}



